<template lang="pug">
    section(class="offers")
        div(class="container")
            div(class="title")
                h2 Our 
                    span offers
            div(class="offers-content")
                div(class="row")
                    div(class="col-md-12 col-lg-4 box" v-for="offer in HomeData.offers", :key="offer.id")
                        div(class="offers-box wow fadeInUp" data-wow-duration="1s")
                            div(class="offers-image")
                                img(:src="offer.image", alt="")
                            div(class="offers-text")
                                p {{ offer.body }}
                            div(class="offers-buttons")
                                a(href="" class="btn hvr-sweep-to-bottom") Request offer
</template>

<script>
import { mapState } from "vuex";
export default {
    name: "Offers",
    mounted() {
        this.$store.dispatch("getHomeData");
    },
    computed: {
        ...mapState(["HomeData"]),
    },
};
</script>
