<template lang="pug">
    div
        section(class="video")
            div(class="container")
                div(class="video-content" :style="{ backgroundImage: `url(${HomeData.Home_image})` }")
                    div(class="video-icon")
                        a(data-fancybox="" :href="HomeData.Home_video"): fa(class="theicon" :icon="['far', 'play-circle']")
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Video",
    mounted() {
        this.$store.dispatch("getHomeData");
    },
    computed: {
        ...mapState(["HomeData"]),
    },
};
</script>
