<template lang="pug">
    section(class="services")
        div(class="container")
            div(class="title")
                h2 Our 
                    span services
                p {{ HomeData.servicesDesc }}
            div(class="services-content")
                div(class="row")
                    div(class="col-md-6 col-lg-3 box" v-for="service in HomeData.services", :key="service.id")
                        div(class="services-box wow fadeInUp" data-wow-duration="1s")
                            div(class="services-box-icon")
                                img(:src="service.image" class="img-fluid" alt="")
                            div(class="services-box-details")
                                h3 {{ service.title }}
                                p {{ service.body }}                 
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Services",
    mounted() {
        this.$store.dispatch("getHomeData");
    },
    computed: {
        ...mapState(["HomeData"]),
    },
};
</script>
