<template lang="pug">
    div
        section(class="solutions")
            div(class="lines")
                div(class="line")
                div(class="line")
            div(class="container")
                div(class="title")
                    h2 Our 
                        span solutions
                div(class="solutions-content")
                    div(class="row" v-for="solution in HomeData.solutions", :key="solution.id")
                        div(class="col-md-7 col-lg-7")
                            div(class="text-part wow fadeInUp" data-wow-duration="1s")
                                h3 {{ solution.title }}
                            div(class="details-part" v-html="solution.body")
                            div(class="buttons-part")
                                a(:href="solution.pdf")
                                    fa(class="theicon" :icon="['far', 'file-pdf']")
                                    | Download pdf 
                                a(:href="solution.video") 
                                    fa(class="theicon" :icon="['fas', 'play']")
                                    | Show video
                        div(class="col-md-5 col-lg-5")
                            div(class="image-part wow fadeInUp" data-wow-duration="1s")
                                img(src="@/assets/images/solutions/sol.png", alt="", class="")
                    .more 
                        a(href="" class="btn hvr-sweep-to-bottom") Request demo
</template>

<script>
import { mapState } from "vuex";

export default {
    name: "Solutions",
    mounted() {
        this.$store.dispatch("getHomeData");
    },
    computed: {
        ...mapState(["HomeData"]),
    },
};
</script>
