<template lang="pug">
    section.slider(dir="ltr")
        .slider-content(:if="HomeData.sliders.length > 0")
            carousel(:autoplay="true" :nav="false" :items="1" :loop="true")
                .single-slide(v-for="value in HomeData.sliders", :key="value.id" :style="{ backgroundImage: `url(${value.image})` }")
                    .container
                        .slider-text
                            h2 {{ value.title }}
                            p {{ value.body }}
</template>

<script>
import carousel from "vue-owl-carousel";
import { mapState } from "vuex";

export default {
    name: "Slider",
    components: { carousel },
    mounted() {
        this.$store.dispatch("getHomeData");
    },
    computed: {
        ...mapState(["HomeData"]),
    },
};
</script>
