import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
        meta: { title: "Home" },
    },
    // {
    //     path: "/:language?",
    //     component: {
    //         render(c) {
    //             return c("router-view");
    //         },
    //     },
    //     children: [
    //         {
    //             path: "/",
    //             name: "Home",
    //             component: Home,
    //         },
    //     ],
    // },
    {
        path: "/about",
        name: "About",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
            import(/* webpackChunkName: "about" */ "../views/About.vue"),
    },
    {
        path: "/services",
        name: "Services",
        component: () => import("../views/OurServices.vue"),
    },
    {
        path: "/solutions",
        name: "Solutions",
        component: () => import("../views/OurSolutions.vue"),
    },
    {
        path: "/portfolio",
        name: "Portfolio",
        component: () => import("../views/OurPortfolio.vue"),
    },
    {
        path: "/blogs",
        name: "Blogs",
        component: () => import("../views/OurBlogs.vue"),
    },
    {
        path: "/plans",
        name: "Plans",
        component: () => import("../views/OurPlans.vue"),
    },
    {
        path: "/contact",
        name: "Contact",
        component: () => import("../views/OurContact.vue"),
    },
    {
        path: "/apply",
        name: "Apply",
        component: () => import("../views/Apply.vue"),
    },
    {
        path: "*",
        name: "NotFoundPage",
        component: () => import("../views/NotFound.vue"),
    },
];

// router.beforeEach((to, from, next) => {
//     let language = to.params.lang;
//     if (!language) {
//         language = "en";
//     }
//     next();
// });

const router = new VueRouter({
    mode: "history",
    base: process.env.BASE_URL,
    routes,
});
export default router;
