<template lang="pug">
    section(class="partners" dir="ltr")
        div(class="container")
            .partners-content
                .partners-carousel(:if="HomeData.partners.length > 0")
                    carousel(:autoplay="true" :nav="false" :dots="true" :items="4" :loop="true")
                        .single-item(v-for="partner in HomeData.partners", :key="partner.id")
                            .inner
                                img(:src="partner.image", class="img-fluid", alt="")
</template>

<script>
import carousel from "vue-owl-carousel";
import { mapState } from "vuex";

export default {
    name: "Partners",
    components: { carousel },
    mounted() {
        this.$store.dispatch("getHomeData");
    },
    computed: {
        ...mapState(["HomeData"]),
    },
};
</script>
